  position: relative;
  top: -20px;
}
.before-after__price {
  font-family: "Oswald Medium", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  top: 30px;
}
.before-after__image-wrapper {
  position: relative;
  top: 75px;
}
.before-after__image {
  height: 232px;
  width: 280px;
  display: none;
}
.before-after__image--show {
  display: block;
}
.before-after .buttons-list__button {
  font-family: "Oswald Medium", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
}
.before-after .before-after__buttons {
  height: 30px;
  width: 210px;
  display: flex;
  position: relative;
  top: 105px;
  margin-left: auto;
  margin-right: auto;
