footer {
  height: 236px;
  width: 320px;
  background-color: #E5E5E5;
}
footer .container {
  display: grid;
  grid-template-rows: 95px 63px 78px;
  grid-auto-columns: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  width: 320px;
  height: 236px;
}
footer .page-footer__logo {
  justify-self: center;
  align-self: center;
  width: 128px;
  height: 24px;
}
footer .social {
  display: block;
  justify-self: center;
  align-self: center;
  width: 130px;
  height: 22px;
}
footer .social__list {
  display: flex;
  justify-content: space-between;
  width: 130px;
  height: 22px;
}
footer .social .social__item {
  justify-self: center;
  align-self: center;
}
footer .social .social__item .social__link--fb {
  background-image: url("/source/img/icon_facebook.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  height: 22px;
  width: 22px;
  background-size: cover;
  display: block;
}
footer .social .social__item .social__link--vk {
  background-image: url("/source/img/icon_vkontakte.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: block;
  height: 13px;
  width: 23px;
  background-size: cover;
}
footer .social .social__item .social__link--ig {
  background-image: url("/source/img/icon_instagram.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: block;
  height: 16px;
  width: 16px;
  background-size: 16px 16px;
}
footer .copyright {
  justify-self: center;
  align-self: center;
  width: 281px;
  height: 34px;
  display: flex;
  justify-content: space-between;
}
footer .copyright__link {
  height: 18px;
  width: 114px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #444444;
  text-decoration: none;
  justify-self: center;
  align-self: center;
}
footer .copyright__link-logo {
  background-image: url("/source/img/htmlacademy.svg");
  height: 34px;
  width: 27px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  justify-self: center;
  align-self: center;
}
